export default {
	data() {
		return {
			// lostname:''
			picker: ['手表', '钥匙', '校园卡','银行卡','耳机','钱包'],
			
			textareaAValue: '',
			form: {
				name: "",
				lostPlace: '',
				index: -1,
				loseTime:this.date+" "+this.time+":00",
				details:'',
				contact: '',
				time: '12:01',
				date: '2018-12-25',
				imgList: [],
				postType:''
			}

		}
	},
	methods: {
		onClick(event) {
			wx.showToast({
				title: `点击标签 ${event.detail.name}`,
				icon: 'none'
			});
		},

		TimeChange(e) {
			this.form.time = e.detail.value
		},
		DateChange(e) {
			this.form.date = e.detail.value
		},
		ChooseImage() {
			uni.chooseImage({
				count: 4, //默认9
				sizeType:["compressed"],
				success: (res) => {
					console.log(res)
					  const tempFilePaths = res.tempFilePaths
					uni.uploadFile({
						url:"http:localhost:8888/upload/img",
						filePath:tempFilePaths[0],
						name:"img",
						fileType:"image",
						// files:res.tempFiles,
						success:(uploadFileRes) => {
							let data=JSON.parse(uploadFileRes.data)
							console.log(data.status)
							if(data.status === 0){
								this.form.imgList.push("http://localhost:8888/img/"+data.data)
							}else{
								
							}						
						}						
					})
				}
			});
		},
		textareaAInput(e) {
			this.textareaAValue = e.detail.value
		},
		textareaBInput(e) {
			this.textareaBValue = e.detail.value
		},
		DelImg(e) {
			uni.showModal({

				content: '要删除这张照片吗？',
				cancelText: '取消',
				confirmText: '删除',
				success: res => {
					if (res.confirm) {
						this.form.imgList.splice(e.currentTarget.dataset.index, 1)
					}
				}
			})
		},
		PickerChange(e) {
			this.form.index = e.detail.value
		},

		subInfo: function() {
			this.$api.addPost(this.form).then(res => {
					uni.showModal({
						content: '发表成功',
						showCancel: false,
					});
					
			})
		}

	}

}
